.timer-bg {
  /* The image used */
  background-image: url(/static/media/background.5d5345f8.png);
  font-family: Ligconsolata;

  /* Full height */
  min-height: 100vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.screensaver {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  -webkit-animation: screensaver 5s linear infinite alternate;
  animation: screensaver 5s linear infinite alternate;
}

.screensaver-logo {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #fe3176;
  font-family: "Brice-Bold";
  font-size: 3.5rem;
  font-weight: 500;
  -webkit-animation: screensaver-logo 4s linear infinite alternate;
  animation: screensaver-logo 4s linear infinite alternate, color_change 5s linear infinite alternate;
}

.TimerContainer {
  z-index: 5;
}

@-webkit-keyframes screensaver {
  100% {
    -webkit-transform: translateX(calc(100vw - 200px));
            transform: translateX(calc(100vw - 200px));
  }
}

@keyframes screensaver {
  100% {
    -webkit-transform: translateX(calc(100vw - 200px));
            transform: translateX(calc(100vw - 200px));
  }
}

@-webkit-keyframes screensaver-logo {
  100% {
    -webkit-transform: translateY(calc(100vh));
            transform: translateY(calc(100vh));
  }
}

@keyframes screensaver-logo {
  100% {
    -webkit-transform: translateY(calc(100vh));
            transform: translateY(calc(100vh));
  }
}

@-webkit-keyframes color_change {
  0% {
    color: #1C1CD2;
  }
  50% {
    color: #DBF429;
  }
  100% {
    color: #FE3176;
  }
}

@keyframes color_change {
  0% {
    color: #1C1CD2;
  }
  50% {
    color: #DBF429;
  }
  100% {
    color: #FE3176;
  }
}

