  align-items: center;
}
@font-face {
  font-family: Brice;
  src: url(/static/media/Brice-Bold.88870cae.otf) format("truetype");
}

@font-face {
  font-family: Inconsolata;
  src: url(/static/media/Inconsolata.d436e6cb.ttf) format("truetype");
}

.tables {
  height: 100%;
  width: 70vw;
  margin: 0 auto;
  border-spacing: 0 5px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 80%;
}

.fixedHeader {
  padding-right: 15px;
}

.heading {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-size: 2rem;
  margin: 1.2rem 0;
  font-family: Brice;
}

.heading th {
  width: 10rem;
}

.scrolldown {
  overflow-y: scroll;
  border-spacing: 0 1rem;
  padding-right: 15px;
}

.scrolldown::-webkit-scrollbar {
  display: block;
  width: 10px;
}

.scrolldown::-webkit-scrollbar-track {
  background-color: transparent;
}

.scrolldown::-webkit-scrollbar-track {
  border-radius: 5px;
}

.scrolldown::-webkit-scrollbar-thumb {
  background: var(--leaderboard-bg);
  border-radius: 5px;
}

.scrolldown::-webkit-scrollbar-track-piece:start {
  background: transparent;
}

.names {
  color: var(--leaderboard-bg);
  height: 2rem;
  font-size: 1.5rem;
  font-family: Inconsolata;
  border-radius: 5%;
  text-align: center;
  font-weight: bolder;
  background-color: black;
}

.names td {
  border: solid 3px var(--leaderboard-bg);
  border-style: solid none;
  padding: 10px 5px;
}

.names td:nth-child(1) {
  width: 20vw;
  border-left-style: solid;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.names td:nth-child(2) {
  width: 60vw;
}

.names td:nth-child(3) {
  width: 20vw;
  border-right-style: solid;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 15px;
}

#me {
  padding: 10px;
  background-color: var(--leaderboard-bg);
  color: black;
}

@media screen and (max-width: 650px) {
  .tables {
    width: 80vw;
  }

  .heading {
    font-size: 1.5rem;
    margin: 1rem 0;
  }

  .names {
    font-size: 1.2rem;
    height: 1.5rem;
  }
}

@media screen and (max-width: 420px) {
  .tables {
    width: 90vw;
  }

  .fixedHeader {
    padding-right: 7px;
  }

  .scrolldown {
    padding-right: 7px;
  }

  .scrolldown::-webkit-scrollbar {
    width: 5px;
  }

  .heading {
    font-size: 1.2rem;
    margin: 0.7rem 0;
  }

  .names {
    font-size: 1rem;
    height: 1rem;
  }
}

.map-section {
  position: relative;
  height: calc(100vh - 80px);
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

#map-loading {
  position: absolute;
  z-index: 10;
  left: 50%;
  top: 50%;
  margin-left: -50px;
