}

.map-container {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: scroll;
  background-color: #151515;
  background-image: url(/static/media/topography.7b2f38af.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
}

#darken {
  position: absolute;
  z-index: 5;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-items: center;
}

.map {
  position: relative;
  display: inline-block;
  width: 1000px;
  height: 1000px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0 auto;
  transition: zoom 0.2s linear;
}

