/* Dialogue Box */

.dialog-container div div {
  background-color: #3b3b3b;
  font-family: "Poppins";
  width: 18rem;
  border-radius: 1rem;
  overflow: visible;
}

#dialog-icon {
  position: absolute;
  width: 6rem;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -3rem;
  left: 6rem;
  font-size: 3rem;
  border-radius: 50%;
  background-color: var(--map-bg);
}

#alert-dialog-title h2 {
  font-size: 1.4rem;
