  font-weight: 700;
  line-height: 110%;
  margin-top: 3rem;
  color: white;
}

#dialog-buttons {
  justify-content: space-evenly;
  margin-bottom: 1rem;
}

#dialog-buttons button {
  background-color: white;
  color: black;
  font-size: 0.9rem;
  width: 4rem;
  border-radius: 1.5rem;
  -webkit-filter: drop-shadow(0.2rem 0.2rem 0.3rem rgba(0, 0, 0, 0.48));
          filter: drop-shadow(0.2rem 0.2rem 0.3rem rgba(0, 0, 0, 0.48));
  font-weight: 600;
}

/* Map zoom icons */

.zoom-buttons {
  position: absolute;
  top: 2rem;
  right: 2rem;
  display: flex;
  flex-direction: row;
  transition: all 0.2s ease-in-out;
}

.zoom-buttons div {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.1rem;
  font-size: 1rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  background-color: var(--map-bg);
  border-radius: 50%;
  transition: all 0.25s ease-in-out;
  margin: 0.3rem;
}

.zoom-buttons div:hover {
  transition: all 0.25s ease-in-out;
  width: 2.4rem;
  height: 2.4rem;
  margin: 0.1rem;
  font-size: 1.2rem;
}

/* Legend box */

.key-button {
  background-color: var(--map-bg);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 2rem;
  right: 2rem;
  height: 3rem;
  width: 3rem;
  padding: 0.5rem;
  font-size: 2rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.legend-box {
  position: absolute;
  bottom: 3rem;
  right: 3rem;
  display: grid;
  grid-template-columns: 0.5fr 2fr;
  grid-row-gap: 0.7rem;
  row-gap: 0.7rem;
  align-items: center;
  padding: 2rem 0.5rem;
  border: 3.5px solid var(--map-bg);
  border-radius: 10px;
  color: white;
  background-color: black;
  font-family: "Poppins";
  font-weight: 550;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.legend-box img {
  justify-self: center;
  width: 60%;
}

.legend-box .solvedNode {
  width: 80%;
}

.legend-box p {
  font-size: 0.9rem;
}

/* Onboarding button */

.tutorial-button {
  background-color: var(--map-bg);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 2rem;
  left: 2rem;
  height: 3rem;
  width: 3rem;
  padding: 0.5rem;
  font-size: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

/* Back button */

.back-button {
  background-color: var(--map-bg);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 2rem;
  left: 2rem;
  height: 3rem;
  width: 3rem;
  padding: 0.5rem;
  font-size: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.react-joyride-step-0 > .react-joyride__spotlight {
  -webkit-transform: translateY(-30px);
