}

.__floater__arrow span svg polygon {
  fill: #3b3b3b;
}

.react-joyride__overlay {
  margin-top: 80px;
  height: calc(100vh - 80px) !important;
}

.react-joyride__tooltip button svg g path {
  fill: white;
}

@media screen and (max-width: 450px) {
  .key-button {
    bottom: 1rem;
    right: 1rem;
  }

  .legend-box {
    bottom: 2rem;
    right: 2rem;
  }

  .tutorial-button {
    bottom: 1rem;
    left: 1rem;
  }

  .zoom-buttons {
    top: 1rem;
    right: 1rem;
  }
